.genre-btn{
    background-color: black;
    border: none;
    border-radius: 50px;
    width: 100px;
    margin-left: 20px;
    margin-top: 15px;
    color: white;
    text-align: center;
    text-decoration-line: none;
}


.genre-btn:hover {
    box-shadow: rgb(246, 84, 174) -2px -2px 0px 2px, rgb(255, 198, 0) 0px 0px 0px 4px, rgba(0, 0, 0, 0.05) 0px 0px 2px 7px;
    transform: scale(1.01);
}

.genre-btn-container {
    width: 60%;
    display: flex;
    flex-flow: row wrap;
    margin: 20px auto;
}