.nav_container {
    position: fixed;
    top: 50px;
    right: 3%;
    z-index: 1;
    display: flex;
    flex-flow: column wrap;
    background-color: #fff;
    padding: 10px 20px;
    box-shadow: 0 13px 24px -5px rgba(50,50,93,0.25), 0 8px 16px -5px rgba(0,0,0,0.3), 0 -6px 16px -5px rgba(0,0,0,0.025);
    border-radius: 5px;
}
.nav_container a {
    margin: 10px;
    padding: 1px 30px !important;
    text-align: center;
    transition: 0.5s;
    background-size: 200% auto;
    color: white;
    border-radius: 10px;
    display: block;
    border: 0px;
    font-weight: 700;
    box-shadow: 0px 0px 14px -7px #f09819;
    background-image: linear-gradient(45deg, #FF512F 0%, #F09819 51%, #FF512F 100%);
    cursor: pointer;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
}

.nav_container a:hover {
    background-position: right center;
    /* change the direction of the change here */
    color: #fff;
    text-decoration: none;
    cursor: pointer;
}

.nav_container a:active {
    transform: scale(0.95);
}

.nav_container a:hover {
    text-decoration: underline dotted #FF512F;
    text-underline-offset: 0.5em !important;
    cursor: pointer;
    text-decoration-thickness: 4px !important;
}